/*
  创建动画
  .createTransition(
    动画标识名,
    {进入动画 - 开始},
    {进入动画 - 结束},
    {离开动画 - 开始},
    {离开动画 - 结束}
  );
  往返动画
  .createTransition(
    动画标识名,
    {进入动画 - 开始 & 离开动画 - 结束},
    {进入动画 - 结束 & 离开动画 - 开始},
  );
 */
/**
 * 淡入淡出
 */
.router-fade-enter {
  opacity: 0;
}
.router-fade-enter.router-fade-enter-active {
  transition: all 0.3s;
  opacity: 1;
}
.router-fade-exit {
  opacity: 1;
}
.router-fade-exit.router-fade-exit-active {
  transition: all 0.3s;
  opacity: 0;
}
/**
 * 页面跳转动画
 */
.router-go-enter {
  transform: translate(100%, 0);
  z-index: 1;
}
.router-go-enter.router-go-enter-active {
  transition: all 0.3s;
  transform: translate(0, 0);
}
.router-go-exit {
  opacity: 1;
}
.router-go-exit.router-go-exit-active {
  transition: all 0.3s;
  opacity: 0;
}
/**
 * 页面返回动画
 */
.router-back-enter {
  opacity: 0;
}
.router-back-enter.router-back-enter-active {
  transition: all 0.3s;
  opacity: 1;
}
.router-back-exit {
  transform: translate(0, 0);
}
.router-back-exit.router-back-exit-active {
  transition: all 0.3s;
  transform: translate(100%, 0);
  z-index: 1;
}
